            "text": "\n// pq \nif (!root || root === p || root === q) return root;\nconst left = lowestCommonAncestor(root.left, p, q); // \nconst right = lowestCommonAncestor(root.right, p, q); // \nif (!left) return right; // \nif (!right) return left; // \nreturn root; // root\n"
