            "text": "\nclass Solution {\n    public void moveZeroes(int[] nums) {\n        // \n        int i = 0;\n        for(int j=0; j<nums.length; j++)\n        {\n            // 0\n            if(nums[j] != 0)\n            {\n                int temp = nums[i];\n                nums[i] = nums[j];\n                nums[j] = temp;\n                i++;\n            }\n        }\n    }\n}\n"
