            "text": "\n\nclass Solution:\n    def isValidSerialization(self, preorder: str) -> bool:\n        diff = 0\n\n        for node in preorder.split(\",\"):\n            diff -= 1\n            if diff < -1:\n                return False\n            if node != \"#\":\n                diff += 2\n        return diff == -1\n\n"
