            "text": "\nclass Solution {\n    public int getSum(int a, int b) {\n        if(a==0) return b;\n        if(b==0) return a;\n\n        while(b!=0)\n        {\n            int carry = a&b;\n            // \n            a = a^b;\n            //1\n            b =carry<<1;\n        }\n        return a;\n    }\n}\n"
