            "text": "\n\nclass Solution:\n    @lru_cache()\n    def superEggDrop(self, K: int, N: int) -> int:\n        if K == 1: return N\n        if N == 0 or N == 1: return N\n        ans = N\n        #  i \n        for i in range(1, N + 1):\n            ans = min(ans, max(self.superEggDrop(K - 1, i - 1) + 1, self.superEggDrop(K,  N - i) + 1))\n        return ans\n"
