        {
            "language": "py",
            "text": "\n\nclass Solution(object):\n    def subarrayBitwiseORs(self, A):\n        pres = set([0])\n        ans = set()\n        for a in A:\n            nxt = set()\n            for pre in pres:\n                nxt.add(a | pre)\n                nxt.add(a)\n            pres = nxt\n            ans |= nxt\n        return len(ans)\n\n\n"
        }
