            "text": "\nclass Solution:\n    def maxScoreWords(self, words, letters, score):\n        ans = 0\n\n        def dfs(start, , counter):\n            if start > len(words): return\n            ans = max(ans, cur)\n            for j in start:\n                if :\n                    dfs(j + 1, , counter)\n\n        dfs(0, 0, collections.Counter(letters))\n        return ans\n"
