import React, { PureComponent } from "react";
import {
  Radio,
  Row,
  Col,
  Input,
  Select,
  message,
  Button,
  Checkbox,
  Spin,
} from "antd";

import {
  copyToClipboard,
  isInExtension,
  getUrlParameter,
  getStorage,
  setStorage,
  debounce,
  getCloundStorage,
} from "../utils.js";

import MarkdownRender from "../components/MarkdownRender";
// import AccessToken from "../components/AccessToken";

const { TextArea } = Input;
const { Option } = Select;

const slogan = `

>  [](https://leetcode-pp.github.io/leetcode-cheat/?tab=solution-template)  

[](https://leetcode-cn.com/u/fe-lucifer/)~

 LeetCode https://github.com/azl397985856/leetcode   40K star 



![](https://tva1.sinaimg.cn/large/007S8ZIlly1gfcuzagjalj30p00dwabs.jpg)`;

function displayLanguage(language) {
  const m = {
    python: "Python",
    python3: "Python3",
    java: "Java",
    cpp: "CPP",
    javascript: "JavaScript",
    go: "Go",
    c: "C",
    "c#": "C#",
    ruby: "Ruby",
    swift: "Swift",
    scala: "Scala",
    kotlin: "Kotlin",
    rust: "Rust",
    php: "PHP",
    typescript: "TypeScript",
  };
  //  Java 
  return m[language] || language;
}

function getTemplate({
  title = "",
  link = "",
  desc = "",
  pre = "",
  company = "",
  language = "python3",
  code = "",
  keyword = "",
  time = "n",
  space = "n",
  isLucifer = false,
}) {
  return `
## (${title})

${link}

## 

\`\`\`
${desc}
\`\`\`

## 

- ${pre}

## 

- ${company}

## 

## 

- ${keyword} 

## 

- ${displayLanguage(language)}

${displayLanguage(language)} Code:

\`\`\`${
    language.toLocaleLowerCase() === "python3"
      ? "python"
      : language.toLocaleLowerCase()
  }

${code}

\`\`\`


****

 n 

- $O(${time})$
- $O(${space})$

${isLucifer ? slogan : ""}
`;
}
function Template({ onChange, template }) {
  return (
    <TextArea
      ref={textAreaRef}
      value={template}
      autoSize
      onChange={onChange}
    ></TextArea>
  );
}

function Complexities({ value, onChange }) {
  return (
    <Radio.Group value={value} onChange={onChange}>
      <Radio.Button value="1">O(1)</Radio.Button>
      <Radio.Button value="\\sqrt n">O(\sqrt n)</Radio.Button>
      <Radio.Button value="logn">O(logn)</Radio.Button>
      <Radio.Button value="n">O(n)</Radio.Button>
      <Radio.Button value="nlogn">O(nlogn)</Radio.Button>
      <Radio.Button value="n^2">O(n^2)</Radio.Button>
      <Radio.Button value="2^n">O(2^n)</Radio.Button>
      <Radio.Button value="n!">O(n!)</Radio.Button>
    </Radio.Group>
  );
}

const formulas = [
  {
    formula: "$sum_{i=1}^{n}{a_i}$",
    name: "",
    logo: require("../imgs/formula/sum.svg"),
  },
  {
    formula: "$a\\times b$",
    name: "",
    logo: require("../imgs/formula/times.svg"),
  },
  {
    formula: "$a\\div b$",
    name: "",
    logo: require("../imgs/formula/div.svg"),
  },
  {
    formula: "$\\frac{a}{b}$",
    name: "",
    logo: require("../imgs/formula/frac.svg"),
  },
  {
    formula: "$a\\approx b$",
    name: "",
    logo: require("../imgs/formula/approx.svg"),
  },
  {
    formula: "$\\sqrt a$",
    name: "",
    logo: require("../imgs/formula/sqrt.svg"),
  },
  {
    formula: String.raw`
$$
  negative[i]=\left\{
  \begin{aligned}
  negative[i-1] + 1 &  & negative[i-1] > 0 \\
  0 & & negative[i-1] = 0 \\
  \end{aligned}
  \right.
$$
    `,
    name: "",
    logo: require("../imgs/formula/choice.svg"),
  },
];

const saveDraft = debounce(
  (v) =>
    setStorage("solution-backup", {
      raw: v,
    }),
  5000
);
const link = getUrlParameter("link") || "";
const title = getUrlParameter("title") || "";
const initialLanguage = getUrlParameter("language")?.toLowerCase() || "python3";
const textAreaRef = React.createRef();
export default class SolutionTemplate extends PureComponent {
  constructor(props) {
    super(props);
    // const [language, setLanguage] = useState(initialLanguage);
    // const [time, setTime] = useState("n");
    // const [space, setSpace] = useState("n");
    // const [isLucifer, setIsLucifer] = useState(false);
    // // const [modalVisible, setModalVisible] = useState(false);
    // const [template, setTemplate] = useState(
    //   getTemplate({
    //     link,
    //     title,
    //     time,
    //     space,
    //     language,
    //   })
    // );
    this.state = {
      language: initialLanguage,
      time: "n",
      space: "n",
      isLucifer: false,
      template: "",
      isloading: false,
    };
    this.setLanguage = (v) =>
      this.setState({
        language: v,
      });
    this.setTime = (v) =>
      this.setState({
        time: v,
      });
    this.setSpace = (v) =>
      this.setState({
        space: v,
      });
    this.setIsLucifer = (v) =>
      this.setState({
        isLucifer: v,
      });
    this.setTemplate = (v) => {
      this.setState({
        template: v,
      });
      // textAreaRef.current.value = v;
    };
  }

  componentDidMount() {
    this.setTemplate(
      getTemplate({
        language: initialLanguage,
        link,
        title,
      })
    );

    getStorage("leetcode-cheatsheet-token")
      .then((res) => res.result.value)
      .then((res) => {
        if (!res.raw) throw new Error("whatever");
        return res;
      })
      .catch(() => ({
        raw: "e574bf60b50d8d2d2db2320ee83aba3cd29cecf2",
      }))
      .then((res) => {
        const t = res.raw;
        if (getUrlParameter("issue_number")) {
          this.setState({
            isloading: true,
          });
          getCloundStorage(getUrlParameter("issue_number"), {
            token: t,
          })
            .then((res) => {
              const { link, title, code, language, desc } = res;

              this.setLanguage(language);
              this.setTemplate(
                getTemplate({
                  desc,
                  language,
                  link,
                  title,
                  code,
                })
              );
              this.setState({
                isloading: false,
              });
            })
            .catch(() =>
              this.setState({
                isloading: false,
              })
            );
        }
      });
  }
  render() {
    const {
      language,
      time,
      space,
      isLucifer,
      template,
      isloading,
    } = this.state;
    return (
      <>
        {/* <AccessToken
        visible={modalVisible}
        onOk={() => setModalVisible(false)}
        onCancel={() => setModalVisible(false)}
      /> */}
        <Button
          type="link"
          href="https://mp.weixin.qq.com/s/mcRFKVWXKmlXaBow6TIwug"
        >
          
        </Button>
        {!isInExtension() ? (
          <>
            <div className="line">
              
              <Select
                value={language}
                style={{ width: 120 }}
                onChange={(v) => {
                  this.setLanguage(v);
                  this.setTemplate(
                    template
                      .replace(new RegExp(`\`\`\`.+`, "mg"), `\`\`\`${v}`)
                      .replace(
                        new RegExp(`.+`, "mg"),
                        `${displayLanguage(v)}`
                      )
                      .replace(
                        new RegExp(`.+ Code:`, "mg"),
                        `${displayLanguage(v)} Code:`
                      )
                  );
                }}
              >
                <Option value="Python3">Python3</Option>
                <Option value="Python">Python</Option>
                <Option value="JavaScript">JavaScript</Option>
                <Option value="C++">CPP</Option>
                <Option value="Java">Java</Option>
                <Option value="GO">Go</Option>
                <Option value="C">C</Option>
                <Option value="C#">C#</Option>
                <Option value="Ruby">Ruby</Option>
                <Option value="Swift">Swift</Option>
                <Option value="Scala">Scala</Option>
                <Option value="Kotlin">Kotlin</Option>
                <Option value="Rust">Rust</Option>
                <Option value="PHP">PHP</Option>
                <Option value="TypeScript">TypeScript</Option>
              </Select>
            </div>
            <div className="line">
              
              {formulas.map(({ formula, name, logo }) => {
                return (
                  <img
                    onClick={() => {
                      copyToClipboard(formula);
                      message.success({
                        content: "~",
                      });
                    }}
                    alt={name}
                    style={{ margin: "0 0 0 20px" }}
                    src={logo}
                    className="problem-icon"
                  />
                );
              })}
            </div>
            <div className="line">
              
              <Complexities
                value={time}
                onChange={(e) => {
                  const v = e.target.value;
                  this.setTime(v);
                  this.setTemplate(
                    template.replace(
                      new RegExp(`.+`, "mg"),
                      `$O(${v})$`
                    )
                  );
                }}
              />
            </div>
            <div className="line">
              
              <Complexities
                value={space}
                onChange={(e) => {
                  const v = e.target.value;
                  this.setSpace(v);
                  this.setTemplate(
                    template.replace(
                      new RegExp(`.+`, "mg"),
                      `$O(${v})$`
                    )
                  );
                }}
              />
            </div>
            <div className="line">
              lucifer 
              <Checkbox
                checked={isLucifer}
                onChange={(e) => {
                  const v = e.target.checked;
                  this.setIsLucifer(v);
                  if (v) {
                    if (template.includes(slogan)) return;
                    this.setTemplate(template + slogan);
                  } else {
                    if (!template.includes(slogan)) return;
                    this.setTemplate(template.replace(slogan, ""));
                  }
                }}
              >
                 lucifer
              </Checkbox>
            </div>

            <Spin spinning={isloading} delay={300}>
              <Row>
                <Col span="12">
                  <div>
                    Markdown:
                    <Button
                      style={{ margin: "10px" }}
                      type="primary"
                      onClick={() => {
                        copyToClipboard(template);
                        message.success({
                          content: "~",
                        });
                      }}
                    >
                       MarkDown 
                    </Button>
                    <Button
                      style={{ margin: "10px" }}
                      onClick={() => {
                        getStorage("solution-backup")
                          .then((res) => res.result.value)
                          .then((res) => {
                            const raw = res.raw;
                            this.setTemplate(raw);
                          })
                          .catch(() =>
                            message.error({
                              content: "",
                            })
                          );
                      }}
                    >
                      
                    </Button>
                  </div>
                  <Template
                    template={template}
                    onChange={(e) => {
                      saveDraft(e.target.value);
                      this.setTemplate(e.target.value);
                    }}
                  />
                </Col>
                <Col span="1"></Col>
                <Col span="11" style={{ marginTop: "20px" }}>
                  <div>:</div>
                  <MarkdownRender source={template}></MarkdownRender>
                </Col>
              </Row>{" "}
            </Spin>
          </>
        ) : (
          <Button
            type="link"
            target="_blank"
            href="https://leetcode-pp.github.io/leetcode-cheat/"
          >
            
          </Button>
        )}
        <ul>
          <li>
            
          </li>
          {/* <li>
           Github API Github
          API 
          <Button type="link" onClick={() => setModalVisible(true)}>
             Access Token
          </Button>
          ( token
          )
        </li> */}
          <li></li>
          <li></li>
          <li></li>
        </ul>
      </>
    );
  }
}
