const dpRangeCodeRecur = `
class Solution:
    def solve(self, s):
        @lru_cache(None)
        def helper(l, r):
            if l >= r:
                return 0
            if s[l] == s[r]:
                return helper(l + 1, r - 1)
            return 1 + min(helper(l + 1, r), helper(l, r - 1))
        return helper(0, len(s) - 1)
`;

