import React from "react";
import { Button, Collapse, Tabs } from "antd";
import Codes from "../components/codes";

import {
  LEETCODE_CN_URL,
  CONTRIBUTE_PROGRAMMING_LANGUAGE_URL,
} from "../constant/index";

const { TabPane } = Tabs;
const { Panel } = Collapse;

export default function CodeTemplate({ tempaltes }) {
  return (
    <div>
      <Tabs>
        {tempaltes.map((tempalte) => (
          <TabPane
            tab={
              <div>
                {tempalte.title}
                <img
                  alt={tempalte.title}
                  style={
                    tempalte.logo
                      ? { margin: "0 0 0 10px" }
                      : { display: "none" }
                  }
                  src={tempalte.logo}
                  className="problem-icon"
                />
              </div>
            }
            key={tempalte.title}
          >
            {tempalte.link && (
              <div>
                 
                <Button type="link" href={tempalte.link} target="_blank">
                  
                </Button>
                ~
              </div>
            )}
            {tempalte.list.map(({ text, problems, codes }) => (
              <Collapse key={text}>
                <Panel header={<div>{text}</div>} key={text}>
                  <div>
                    
                    <ul>
                      {problems.map((problem) => (
                        <li key={problem.title}>
                          <Button type="text">{problem.title}</Button>
                          <Button
                            onClick={(e) => e.stopPropagation()}
                            type="link"
                            href={`${LEETCODE_CN_URL}/problems/${problem.id}`}
                            target="_blank"
                            size="small"
                            style={{ marginLeft: "10px" }}
                          >
                            
                          </Button>
                        </li>
                      ))}
                    </ul>
                  </div>
                  <Codes codes={codes} />
                  <Button
                    type="link"
                    href={CONTRIBUTE_PROGRAMMING_LANGUAGE_URL}
                  >
                     or 
                  </Button>
                </Panel>
              </Collapse>
            ))}
          </TabPane>
        ))}

        <TabPane tab="" key="more" disabled></TabPane>
      </Tabs>
    </div>
  );
}
